<template>
  <div>
    <ul class="capital">
      <li v-for="(item,index) of cities" :key="index" @click="handleTurn(index)">{{index}}</li>
      <li class="top"></li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "CityWord",
  props: ["cities"],
  methods: {
    handleTurn(index) {
      this.$store.commit("changeLetter",index);
    },
  }
};
</script>
<style lang="scss" scoped>
@import "~@/assets/style/scss/global";
.capital {
  position: fixed;
  top: 110px;
  right: 5px;
  line-height: 24px;
  text-align: center;
  color: color(bg);
  .top{
      color:#000;
      font-weight: bold;
  }
}
</style>
